import React from 'react';
import './ShopCardItem.scss';
import { LazyImage } from './LazyImage';
import { ExternalLink } from './ExternalLink';

type ShopCardItemProps = {
  image: string;
  title: string;
  imageRatio: number;
  url: string;
};

const ShopCardItem: React.FC<ShopCardItemProps> = (props) => {

  const imageStyles: React.CSSProperties = {};
  if (props.imageRatio < 1) imageStyles.maxWidth = `${100 * props.imageRatio * props.imageRatio}%`;

  return (
    <div className="ShopCardItem">
      <div className="ShopCardItem__imageWrapper">
        <ExternalLink href={props.url} className="ShopCardItem__image u-anchor" style={imageStyles}>
          <LazyImage
            className="ShopCardItem__lazyImage"
            src={props.image}
            ratio={props.imageRatio}
          />
          <div className="ShopCardItem__flare"></div>
          <div className="ShopCardItem__reflection"></div>
        </ExternalLink>
      </div>
      <div className="ShopCardItem__text">
        <h4 className="ShopCardItem__title">{props.title}</h4>
        <ExternalLink className="button" href={props.url}>Buy Print</ExternalLink>
      </div>
    </div>
  );
};

export { ShopCardItem };
